import Vue from 'vue'
import App from './App.vue'


import BootstrapVue from 'bootstrap-vue'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

Vue.use(BootstrapVue);


import { library } from '@fortawesome/fontawesome-svg-core'
import { faBold, faItalic, faList, faCode, faStrikethrough, faListOl, faListUl, faUnderline, faUndo, faRedo } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faBold, faItalic, faList, faListOl, faListUl, faCode, faStrikethrough, faUnderline, faUndo, faRedo)
Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false


new Vue({
  render: h => h(App),
}).$mount('#app')
