<template>
  <div id="app">
      <div class="container">
          <div class="task-manager-container py-5">
              <div class="row">
                  <div class="col-4">
                      <div class="card">
                          <div class="card-header">
                              <button class="btn btn-block btn-outline-secondary" @click="creatingTask = false" v-if="creatingTask">
                                  Отмена
                              </button>
                              <button class="btn btn-block btn-outline-secondary" @click="runCreateTask" v-else>
                                  Новая заметка
                              </button>
                          </div>
                          <b-list-group class="tasks-list">
                              <b-list-group-item
                                      v-for="task in tasks"
                                      :key="task.id"
                                      href="#"
                                      :active="chosenTaskId === task.id"
                                      class="flex-column align-items-start"
                                      @click="chosenTaskId = task.id"
                              >
                                  <div class="d-flex w-100 justify-content-between">
                                      <h5 class="mb-1">{{ task.title }}</h5>
                                      <small>#{{ task.id }}</small>
                                  </div>
                                  <p class="mb-1" v-html="strip(trim(task.content, 60))"></p>
                                  <small>Добавлено: {{ task.createdAt | timestampToDate }}</small>
                              </b-list-group-item>
                          </b-list-group>
                      </div>
                  </div>

                  <div class="col-8">
                      <div class="card">
                          <div class="card-body">
                              <div v-if="creatingTask">
                                  <b-form>
                                      <b-form-group
                                        id="title-input-group"
                                        label="Название:"
                                        label-for="title-input"
                                      >
                                          <b-form-input
                                            id="title-input"
                                            v-model="newTask.title"
                                            required
                                          />
                                      </b-form-group>
                                      <b-form-group
                                        id="content-input-group"
                                        label="Текст:"
                                        label-for="text-input"
                                      >
                                          <editor-menu-bar :editor="editor" v-slot="{ commands, isActive, focused }">
                                              <div class="menubar">
                                                  <button v-for="command in editorCommandsButtons" :key="command.command"
                                                          class="menubar__button"
                                                          @click="commands[command.command]()"
                                                          :class="{'is-active': command.toggleable && isActive[command.command]()}"
                                                          type="button"
                                                  >
                                                      <font-awesome-icon :icon="command.icon"/>
                                                  </button>
                                              </div>
                                          </editor-menu-bar>
                                          <editor-content :editor="editor" id="text-input" class="rounded p-1 mb-0" style="border: 1px solid #ced4da;" />
                                      </b-form-group>
                                      <b-button @click="saveTask">Сохранить</b-button>
                                  </b-form>
                              </div>
                              <div v-else>
                                  <div v-if="chosenTaskId">
                                      <h5 class="card-title"><span class="text-muted">#{{ chosenTask.id }}</span> {{ chosenTask.title }}</h5>
                                      <h6 class="card-subtitle text-muted"><small>Добавлено: {{ chosenTask.createdAt | timestampToDate('D MMMM YYYY, HH:mm') }}</small></h6>

                                      <div class="mt-4" v-html="chosenTask.content"></div>
                                  </div>
                                  <div v-else>
                                      Выбери заметку или создай новую
                                  </div>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
import { Editor, EditorContent, EditorMenuBar } from 'tiptap'
import moment from 'moment';

import {
    CodeBlock,
    HardBreak,
    Heading,
    OrderedList,
    BulletList,
    ListItem,
    Bold,
    Code,
    Italic,
    Link,
    Strike,
    Underline,
    History,
} from 'tiptap-extensions'

const apiHost = 'https://task-manager.bobroid.win';

export default {
    name: 'app',
    data () {
        return {
            editorCommandsButtons: [
                {
                    command: 'bold',
                    icon: 'bold',
                    toggleable: true
                },
                {
                    command: 'italic',
                    icon: 'italic',
                    toggleable: true
                },
                {
                    command: 'strike',
                    icon: 'strikethrough',
                    toggleable: true
                },
                {
                    command: 'underline',
                    icon: 'underline',
                    toggleable: true
                },
                {
                    command: 'code',
                    icon: 'code',
                    toggleable: true
                },
                {
                    command: 'bullet_list',
                    icon: 'list-ul',
                    toggleable: true
                },
                {
                    command: 'ordered_list',
                    icon: 'list-ol',
                    toggleable: true
                },
                {
                    command: 'undo',
                    icon: 'undo',
                },
                {
                    command: 'redo',
                    icon: 'redo'
                }
            ],
            chosenTaskId: null,
            creatingTask: false,
            editor: false,
            newTask: {
                title: '',
                text: ''
            },
            tasks: []
        };
    },
    computed: {
        chosenTask () {
            let task;

            this.tasks.forEach((item) => {
                if (item.id === this.chosenTaskId) task = item;
            });

            return task;
        }
    },
    components: {
        EditorContent, EditorMenuBar
    },
    mounted () {
        this.updateTasksList();

        moment.locale('ru')
    },
    filters: {
        timestampToDate (timestamp, format) {
            let momentObj = moment.unix(timestamp);

            if (format === undefined) format = 'D MMM HH:mm';

            return momentObj.format(format);
        }
    },
    watch: {
        creatingTask (val) {
            if (val) {
                this.editor = new Editor({
                    extensions: [
                        new CodeBlock(),
                        new HardBreak(),
                        new Heading({ levels: [1, 2, 3] }),
                        new BulletList(),
                        new OrderedList(),
                        new ListItem(),
                        new Bold(),
                        new Code(),
                        new Italic(),
                        new Link(),
                        new Strike(),
                        new Underline(),
                        new History(),
                    ],
                    onUpdate: ({getHTML}) => {
                        this.newTask.text = getHTML();
                    }
                })
            } else if (this.editor !== undefined){
                this.editor.destroy();
            }
        }
    },
    methods: {
        runCreateTask () {
            this.creatingTask = true;
            this.chosenTaskId = null;
        },
        trim (string, length) {
            if (string === null) return '';

            return string.slice(0, length)
        },
        strip (html) {
            let tmp = document.createElement('div');
            tmp.innerHTML = html;

            return tmp.textContent || tmp.innerText || '';
        },
        saveTask (e) {
            e.preventDefault();

            fetch(apiHost + '/tasks', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                },
                body: JSON.stringify({title: this.newTask.title, content: this.newTask.text})
            })
                .then((response) => {
                    if (!response.ok) {
                        return response.text().then((text) => {
                            return Promise.reject(text);
                        });
                    }

                    this.updateTasksList();
                    this.creatingTask = false;
                    this.newTask.title = null;

                    return response.json();
                })
                .catch(() => {

                });
        },
        updateTasksList () {
            fetch(apiHost + '/tasks?sort=-id')
                .then((response) => {
                    return response.json();
                })
                .then((data) => {
                    this.tasks = data;
                });
        }
    },
}
</script>


